/*--------------------------
    Import
--------------------------*/
import React, { Component } from 'react';
import styled from 'styled-components';
import { IoIosArrowDown, IoIosArrowUp } from "react-icons/io";
import SelectDate from './commons/SelectDate'
import AddressInputs from './commons/AddressInputs';
import PhoneGroup from './commons/PhoneGroup';

/*--------------------------
    Styled
--------------------------*/
const Container = styled.div`
    margin-top: 20px;
    box-sizing: border-box;
    * {
      box-sizing: border-box;
    }
`;
const TitleBar = styled.div`
    width: 100%;
    height: 52px;
    border-radius: 2px;
    background-color: #F8F5F5;
    color: #333;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 16px;
    font-size: 14px;
`;
const ToggleBtn = styled.div`
    color: #333;
    font-size: 14px;
    display: flex;
    align-items: center;
    cursor: pointer;
    > svg {
        margin-left: 16px;
        font-size: 20px;
    }
`;
const FormArea = styled.div`
    overflow: ${props => props.show ? 'visiable' : 'hidden'};
    padding: ${props => props.show ? '20px 0' : 0};
    height: ${props => props.show ? 'auto' : 0};
`;
const FormRow = styled.div`
    width: 100%;
    margin-bottom: 20px;
    display: flex;
`;
const FormItem = styled.div`
    display: flex;
    align-items: center;
    width: ${props => props.colspan ? props.colspan * 33.3333 : 33.3333}%;
    padding: ${props => {
    switch (props.position) {
      case 1:
        return '0 20px 0 0'
      case 2:
        return '0 20px 0 0'
      case 3:
        return '0'
      default:
        return ''
    }
  }};
    justify-content: ${props => {
    switch (props.position) {
      case 1:
        return 'flex-start'
      case 2:
        return 'center'
      case 3:
        return 'flex-end'
      default:
        return ''
    }
  }};
`;
const Label = styled.div`
    font-weight: bold;
    color: #333;
    font-size: 14px;
    min-width: 70px;
    text-align: right;
    margin-right: 20px;
    display: flex;
    align-items: center;
`;
const Required = styled.div`
    margin-right: 3px;
    font-size: 14px;
    color: #f33;
`;
const TextInput = styled.input`
    flex: 2;
    /* max-width: 300px; */
    height: 40px;
    border-radius: 3px;
    border: 1px solid ${props => props.readOnly ? 'transparent' : props.isError ? '#f33' : '#ddd'};
    font-size: 14px;
    padding-left: 10px;
    color: #333;
`;
const RadioBtn = styled.div`
    /* width: 145px; */
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    border-radius: 3px;
    border: 1px solid ${props => props.isError ? '#f33' : props.active ? '#333' : '#ddd'};
    color: ${props => props.active ? '#333' : '#ddd'};
    margin-right: 10px;
    &:last-child {
        margin-right: 0;
    }
    cursor: pointer;
`;
const SelectArea = styled.div`
    flex: 2;
    /* max-width: 300px; */
    height: 40px;
    position: relative;
`;
const Select = styled.select`
    width: 100%;
    height: 100%;
    border-radius: 3px;
    border: 1px solid ${props => props.isError ? '#f33' : '#ddd'};
    /* border: 1px solid #ddd; */
    font-size: 14px;
    padding-left: 10px;
    color: ${
  props => props.hasValue ?
    '#333'
    :
    '#777'
  };
    background-color: #fff;
    -webkit-appearance: none;
`;
const SelectReadonly = styled.div`
    width: 100%;
    height: 100%;
    border-radius: 3px;
    /* border: 1px solid #ddd; */
    font-size: 14px;
    padding-left: 10px;
    color: #333;
    display: flex;
    align-items: center;
    background-color: #fff;
`;
const SelectArrowWrapper = styled.div`
    width: 40px;
    height: 40px;
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    user-select: none;
    svg {
        font-size: 20px;
        color: #cdcdcd;
    }
`;
const Textarea = styled.textarea`
    flex: 2;
    /* max-width: 300px; */
    min-height: 40px;
    border-radius: 3px;
    border: 1px solid ${props => props.isError ? '#f33' : '#ddd'};
    font-size: 14px;
    padding-left: 10px;
    color: #333;
`;
/*--------------------------
    Main Component
--------------------------*/
class FormGroup extends Component {

  // static propTypes = {
  // };

  constructor(props) {
    super(props);

    this.state = {
      isOpen: true,
      formGroupErrorMap: {}
    };
    this.toggleOpen = this.toggleOpen.bind(this)
    this.createRows = this.createRows.bind(this)
    this.onChange = this.onChange.bind(this)
    this.onDateChange = this.onDateChange.bind(this)
    this.resetErrorMap = this.resetErrorMap.bind(this)
    this.onPhonChange = this.onPhonChange.bind(this)
    this.clearValue = this.clearValue.bind(this)
    this.getSelectOptionsValue = this.getSelectOptionsValue.bind(this)
  }

  componentDidMount() {
  }
  onChange(e, fieldName) {
    const {
      changeInput
    } = this.props
    this.setState({
      [fieldName]: e.target.value
    })
    changeInput && changeInput(e.target.value, fieldName)
    this.resetErrorMap(fieldName)
  }
  radioChange(value, fieldName) {
    const {
      changeInput
    } = this.props
    this.setState({
      [fieldName]: value
    })
    changeInput && changeInput(value, fieldName)
    this.resetErrorMap(fieldName)
  }
  setDefaultInput(value, fieldName) {
    this.setState({
      [fieldName]: value
    })
  }
  createRows() {
    const {
      inputs
    } = this.props
    let rows = []
    let rowIdx = 0
    let rowItemCount = 0
    for (let i = 0; i < inputs.length; i++) {
      let input = inputs[i]
      let colspan = input.colspan || 1
      rowItemCount += colspan
      if (rowItemCount > 3) {
        rowIdx++
        rowItemCount = colspan
      }
      if (Array.isArray(rows[rowIdx])) {
        rows[rowIdx].push(input)
      } else {
        rows[rowIdx] = []
        rows[rowIdx].push(input)
      }
    }
    return rows
  }
  toggleOpen() {
    this.setState({
      isOpen: !this.state.isOpen
    })
  }
  onDateChange(fieldName, time) {
    const {
      changeInput
    } = this.props
    changeInput && changeInput(time, fieldName)
    this.resetErrorMap(fieldName)
  }
  onAddressChange(fieldName, address) {
    const {
      changeInput
    } = this.props
    changeInput && changeInput(address, fieldName)
    this.resetErrorMap(fieldName)
  }
  resetErrorMap(fieldName) {
    const {
      resetErrorMap
    } = this.props
    resetErrorMap && resetErrorMap(fieldName)
  }
  checkboxChenge(fieldName, value) {
    const {
      changeInput
    } = this.props
    let stateValues = this.state[fieldName] || []
    const valueIdx = stateValues.indexOf(value)
    if (valueIdx < 0) {
      stateValues.push(value)
    } else {
      stateValues.splice(valueIdx, 1)
    }
    this.setState({
      [fieldName]: stateValues
    })
    changeInput && changeInput(stateValues, fieldName)
    this.resetErrorMap(fieldName)
  }
  onPhonChange(values, fieldName) {
    const {
      changeInput
    } = this.props
    this.setState({
      [fieldName]: values
    })
    changeInput && changeInput(values, fieldName)
    this.resetErrorMap(fieldName)
  }
  getSelectOptionsValue(inputValue, inputOptions) {
    let inputOptionArr = inputOptions.filter((option) => {
      return option.value === inputValue
    })
    const inputOption = inputOptionArr[0]
    return inputOption.label
  }
  clearValue(fieldName) {
    const {
      clearValue
    } = this.props
    clearValue && clearValue(fieldName)
  }

  render() {
    const {
      isOpen
    } = this.state
    const {
      groupTitle,
      inputs,
      errMap
    } = this.props
    const rows = this.createRows(inputs)
    let position = 1
    let tempColspan = 1
    return (
      <Container>
        {groupTitle ?
          <TitleBar>
            {groupTitle}
            <ToggleBtn
              onClick={this.toggleOpen}
            >
              {isOpen ? '收起' : '展開'}
              {isOpen ? <IoIosArrowUp /> : <IoIosArrowDown />}
            </ToggleBtn>
          </TitleBar>
          :
          null
        }
        <FormArea
          show={isOpen}
        >
          {rows.map((row, idx) => {
            return (
              <FormRow
                key={idx}
              >
                {row.map((input, idx) => {
                  // console.log(idx)
                  if (idx === 0) {
                    position = 1
                    tempColspan = input.colspan || 1
                  } else position = position + tempColspan
                  return (
                    <FormItem
                      key={idx}
                      colspan={input.colspan || 1}
                      isLast={row.length === idx + 1}
                      position={position}
                    >
                      <Label>
                        {input.required ?
                          <Required>*</Required>
                          :
                          null
                        }
                        {input.columnName}
                      </Label>
                      {input.columnType === 'text' ?
                        <TextInput
                          onChange={(e) => this.onChange(e, input.columnId)}
                          value={this.state[input.columnId] || input.value || ''}
                          isError={errMap[input.columnId]}
                          readOnly={input.readOnly}
                        />
                        :
                        null
                      }
                      {input.columnType === 'radio' ?
                        input.options.map(option => {
                          return (
                            <RadioBtn
                              active={this.state[input.columnId] === option.value || (!this.state[input.columnId] && input.value === option.value)}
                              key={option.label}
                              onClick={input.readOnly ? () => { return } : () => this.radioChange(option.value, input.columnId)}
                              isError={errMap[input.columnId]}
                            >{option.label}</RadioBtn>
                          )
                        })
                        :
                        null
                      }
                      {input.columnType === 'date' ?
                        <SelectDate
                          onDateChange={(time) => this.onDateChange(input.columnId, time)}
                          time={this.state[input.columnId] || input.value || 0}
                          readOnly={input.readOnly}
                          isError={errMap[input.columnId]}
                        />
                        :
                        null
                      }
                      {input.columnType === 'address' ?
                        <AddressInputs
                          address={this.state[input.columnId] || input.value || undefined}
                          onAddressChange={address => this.onAddressChange(input.columnId, address)}
                          setAddressError={() => this.clearValue(input.columnId)}
                          isError={errMap[input.columnId]}
                        />
                        :
                        null
                      }
                      {input.columnType === 'select' ?
                        <SelectArea>
                          {input.value && input.readOnly ?
                            null
                            :
                            <SelectArrowWrapper>
                              <IoIosArrowDown />
                            </SelectArrowWrapper>
                          }
                          {input.value && input.readOnly ?
                            <SelectReadonly>
                              {this.getSelectOptionsValue(input.value, input.options)
                              }
                            </SelectReadonly>
                            :
                            <Select
                              value={this.state[input.columnId] || input.value || ""}
                              hasValue={this.state[input.columnId] || input.value}
                              onChange={e => this.onChange(e, input.columnId)}
                              isError={errMap[input.columnId]}
                            >
                              <option value="" disabled>請選擇{input.columnName}</option>
                              {input.options.map((option, index) => {
                                const {
                                  label,
                                  value
                                } = option
                                return (
                                  <option
                                    key={value}
                                    value={value}
                                  >
                                    {label}
                                  </option>
                                )
                              })

                              }
                            </Select>
                          }
                        </SelectArea>
                        :
                        null
                      }
                      {input.columnType === 'phone' ?
                        <PhoneGroup
                          onChange={(values) => this.onPhonChange(values, input.columnId)}
                          phones={input.value || undefined}
                          // value={this.state[input.columnId] === '' ? '' : this.state[input.columnId] || input.value || ''}
                          isError={errMap[input.columnId]}
                          setPhoneError={() => this.clearValue(input.columnId)}
                        />
                        :
                        null
                      }
                      {input.columnType === 'checkbox' ?
                        input.options.map((option, idx) => {
                          return (
                            <RadioBtn
                              active={(this.state[input.columnId] && Array.isArray(this.state[input.columnId]) && this.state[input.columnId].indexOf(option.value) > -1) || (Array.isArray(input.value) && input.value.indexOf(option.value) > - 1)}
                              key={option.label}
                              onClick={input.readOnly ? () => { return } : () => this.checkboxChenge(input.columnId, option.value)}
                              isError={errMap[input.columnId]}
                            >{option.label}</RadioBtn>
                          )
                        })
                        :
                        null
                      }
                      {input.columnType === 'textarea' ?
                        <Textarea
                          onChange={(e) => this.onChange(e, input.columnId)}
                          value={this.state[input.columnId] === '' ? '' : this.state[input.columnId] || input.value || ''}
                          isError={errMap[input.columnId]}
                        />
                        :
                        null
                      }

                    </FormItem>
                  )
                })}
              </FormRow>
            )
          })

          }
          {
            this.props.children
          }
        </FormArea>
      </Container>
    );
  }
}

/*--------------------------
    Export
--------------------------*/
export default FormGroup