/*--------------------------
    Main Component
--------------------------*/
class SelectDate extends Component {

  // static propTypes = {
  // };

  constructor(props) {
    super(props);

    this.state = {
      year: 1980,
      month: 1,
      day: 1,
      days: [],
      time: null,
    };
    this.init = this.init.bind(this)
    this.createYear = this.createYear.bind(this)
    this.createDay = this.createDay.bind(this)
    this.changeYear = this.changeYear.bind(this)
    this.changeMonth = this.changeMonth.bind(this)
    this.changeDay = this.changeDay.bind(this)
    this.submitDate = this.submitDate.bind(this)
    this.changeTime = this.changeTime.bind(this)
  }

  componentDidMount() {
    this.init()
  }
  init() {
    const {
      time
    } = this.props
    const {
      year,
      month,
      // day
    } = this.state
    if (time) {
      const date = new Date(time)
      const initYear = date.getFullYear()
      const initMonth = date.getMonth()
      const initDay = date.getDate()
      this.setState({
        time: `${initYear}/${initMonth + 1}/${initDay}`
      })
      this.setState({
        year: initYear,
        month: initMonth + 1,
        days: this.createDay(Number(initYear), Number(initMonth))
      }, () => {
        this.setState({
          day: Number(initDay)
        })
      })
    } else {
      this.setState({
        year: 1980,
        month: 1,
        days: this.createDay(year, month, 0)
      }, () => {
        this.setState({
          day: 1
        })
      })
    }
  }
  createYear() {
    let yearArr = []
    const yearNow = new Date().getFullYear()
    for (let i = yearNow - 100; i <= yearNow + 10; i++) {
      yearArr.push(i)
    }
    return yearArr
  }
  createDay(year, month) {
    let days = []
    const daysOfMonth = new Date(year, month, 0).getDate()
    for (let i = 1; i <= daysOfMonth; i++) {
      days.push(i)
    }
    return days
  }
  changeYear(e) {
    const {
      month
    } = this.state
    this.setState({
      year: e.target.value,
      days: this.createDay(e.target.value, month, 0)
    }, () => {
      this.submitDate()
    })
  }
  changeMonth(e) {
    const {
      year
    } = this.state
    this.setState({
      month: e.target.value,
      days: this.createDay(year, e.target.value, 0)
    }, () => {
      this.submitDate()
    })
  }
  changeDay(e) {
    this.setState({
      day: e.target.value
    }, () => {
      this.submitDate()
    })
  }
  submitDate() {
    const {
      year,
      month,
      day
    } = this.state
    this.props.onDateChange && this.props.onDateChange({
      year: String(year),
      month: String(month),
      day: String(day)
    })
  }
  changeTime(time) {
    const dateTime = new Date(time)
    const year = dateTime.getFullYear()
    let month = String(dateTime.getMonth() + 1)
    let day = String(dateTime.getDate())
    if (month.length < 2) month = '0' + month
    if (day.length < 2) day = '0' + day
    this.props.onDateChange && this.props.onDateChange(`${year}${month}${day}`)
  }

  render() {
    const {
      time
    } = this.state
    const {
      isError
    } = this.props
    return (
      <Wrapper
        isError={isError}
      >
        <DateInput
          format="YYYY/MM/DD"
          value={time || undefined}
          formatDate={formatDate}
          parseDate={parseDate}
          onDayChange={day => this.changeTime(day)}
          placeholder="YYYY/MM/DD"

        />
      </Wrapper>
    );
  }
}
/*--------------------------
    Export
--------------------------*/
export default SelectDate