import React, { Component } from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import actionCreators from '../actions';
import styled, { keyframes } from 'styled-components';


const rotate360 = keyframes`
	from {
		transform: rotate(0deg);
	}

	to {
		transform: rotate(360deg);
	}
`;

// 包覆每個 videoItem 的 div
const VideoItem = styled.div`
	width: 1200px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 20px;
	border: 2px solid #333;
	padding: 5px;
`;

// iframe 的外觀
const VideoIframe = styled.iframe`
	width: 280px;
`;

// 影片介紹的區塊
const VideoIntro = styled.div`
	width: 900px;
`;

// 影片標題
const VideoTitle = styled.h3`
	font-size: 20px;
`;

// 影片敘述
const VideoDesc = styled.p`
	font-size: 12px;
`;

// 狀態
const Status = styled.div`
	width: 100%;
	padding: 20px 0;
	text-align: center;
	color: #666;
	font-size: 14px;
`;
const Spin = styled.div`
	position: relative;
	vertical-align: sub;
	display: inline-block;
	width: 20px;
	height: 20px;
	margin: 0 auto;
	border-width: 2px;
	border-style: solid;
	border-top-color: transparent;
	border-left-color: transparent;
	border-right-color: #f33;
	border-bottom-color: #f33;
	border-radius: 100%;
	animation: ${rotate360} 0.6s linear infinite;
`;

class List extends Component {
	constructor(props) {
		super(props)
		this.getMoreResult = this.getMoreResult.bind(this)
		this.onScroll = this.onScroll.bind(this)
		// 建立 reference 取得 dom 節點
		this.listContainer = React.createRef()
	}
	componentDidMount() {
		// 載入時執行一次呼叫 api
		this.props.playlistActinos.getPlaylistItems({})
		// 註冊 scroll 事件
		window.addEventListener('scroll', this.onScroll)
	}
	componentWillUnmount() {
		// will unmount 的時候要取消註冊
		window.removeEventListener('scroll', this.onScroll)
	}
	onScroll(e) {
		const {
			isLoading,
			isNoMore
		} = this.props
		// scroll 時判斷 如果符合條件才再次呼叫 api 取得接下來的資料
		if(!isLoading && !isNoMore && this.isBottom(this.listContainer)) {
			this.getMoreResult()
		}
	}
	// 傳入 element 判斷是否滾動到這個 element 的底部
	isBottom(el) {
		return el.current.getBoundingClientRect().bottom <= window.innerHeight
	}
	getMoreResult() {
		const {
			nextPageToken,
			isLoading,
			isNoMore
		} = this.props
		// 如果已經沒有資料 或是正在讀取中 就不再呼叫 api 取得資料
		if(isLoading || isNoMore) return
		this.props.playlistActinos.getPlaylistItems({nextPageToken})
	}
	render() {
		const {
			ids,
			videosCache,
			isLoading,
			isNoMore,
			isError
		} = this.props

		return (
			<div
				ref={this.listContainer}
			>
				{ids.map((id, idx) => {
					const video = videosCache[id]
					const {
						snippet
					} = video
					const {
						title,
						description,
						resourceId
					} = snippet
					const {
						videoId
					} = resourceId
					return (
						<VideoItem 
							key={id}
						>
							<VideoIframe 
								src={`https://www.youtube.com/embed/${videoId}`}
							/>
							<VideoIntro>
								<VideoTitle>
									{title}
								</VideoTitle>
								<VideoDesc>
									{description}
								</VideoDesc>
							</VideoIntro>

						</VideoItem>
					)
				})

				}
				{isLoading ?
					<Spin/>
					:
					null
				}
				{isNoMore ?
					<Status>沒有更多資料了</Status>
					:
					null
				}
				{isError ?
					<Status>出錯了</Status>
					:
					null
				}
				
				<button onClick={this.getMoreResult}>getMore</button>
			</div>
		)
	}
}


const mapStateToProps = state => {
	return {
		ids: state.videos.ids,
		videosCache: state.videos.videosCache,
		isLoading: state.videos.isLoading,
		isError: state.videos.isError,
		nextPageToken: state.videos.nextPageToken,
		isNoMore: state.videos.isNoMore,
	};
};

const mapActionToProps = function (dispatch) {
	return {
		playlistActinos: bindActionCreators(actionCreators.playlistActionCreators, dispatch)
	};
};

export default connect(mapStateToProps, mapActionToProps, null)(List);
