import actionTypes from '../actions/actionTypes'

const reducers = {
    // [actionTypes.addTodo]: function (state, action) {
    //     return addTodo(state, action)
    // },
    [actionTypes.getPlaylistItemsStart]: function (state, action) {
        return getPlaylistItemsStart(state, action)
    },
    [actionTypes.getPlaylistItemsError]: function (state, action) {
        return getPlaylistItemsError(state, action)
    },
    [actionTypes.getPlaylistItemsSuccess]: function (state, action) {
        return getPlaylistItemsSuccess(state, action)
    },
}

const getPlaylistItemsError = (state, action) => {
    const {
        videos
    } = state
    const new_videos = Object.assign({}, videos, {
        isLoading: false,
        isError: true
    })
    return Object.assign({}, state, {
        videos: new_videos
    })
}


const getPlaylistItemsStart = (state, action) => {
    const {
        videos
    } = state
    const new_videos = Object.assign({}, videos, {
        isLoading: true
    })
    return Object.assign({}, state, {
        videos: new_videos
    })
}

const getPlaylistItemsSuccess = (state, action) => {
    const {
        items,
        nextPageToken,
        pageInfo
    } = action.payload
    const {
        totalResults
    } = pageInfo
    const {
        videos
    } = state

    const {
        ids,
        videosCache
    } = videos

    let responseIds = []
    let new_videosCache = videosCache

    for (let i = 0; i < items.length; i++) {
        responseIds.push(items[i].id)
        new_videosCache = Object.assign({}, new_videosCache, {
            [items[i].id]: items[i]
        })
    }

    const new_ids = ids.concat(responseIds)

    const new_videos = Object.assign({}, videos, {
        ids: new_ids,
        videosCache: new_videosCache,
        nextPageToken,
        isNoMore: totalResults === new_ids.length,
        isLoading: false,
        isError: false,
    })

    return Object.assign({}, state, {
        videos: new_videos
    })
}

// const addTodo = (state, action) => {
//     return state
// }



export default function createReducers(initState) {
    return function reducer(state = initState, action) {
        if (reducers.hasOwnProperty(action.type)) {
            return  reducers[action.type](state, action);
        } else {
            return state;
        }
    };
}