import React from 'react';
import ReactDOM from 'react-dom';
import { Route, HashRouter } from 'react-router-dom'
import App from './App';
import Playlist from './components/Playlist'
import FormSettingView from './components/FormSettingView'
import AboutView from './components/AboutView'
import WorkView from './components/WorkView'
import * as serviceWorker from './serviceWorker';
import './App.css'

ReactDOM.render((
  <HashRouter>
    <Route path="/" component={App} exact />
    <Route path="/myproject/playlistitemapi" component={Playlist} exact />
    <Route path="/myproject/formgenerator" component={FormSettingView} exact />
    <Route path="/about" component={AboutView} exact />
    <Route path="/work" component={WorkView} exact />
  </HashRouter>
), document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
