import React, { Component } from 'react'
import styled from 'styled-components'
import { IoIosArrowDown, IoIosAddCircleOutline, IoMdRemoveCircleOutline } from "react-icons/io";
import FormGroup from './FormGroup'

const Container = styled.div`
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  * {
    box-sizing: border-box;
  }
`;
const GroupSettingArea = styled.div`
  width: 100%;
  margin-bottom: 30px;
`;
const ErrMsg = styled.div`
  margin-left: 20px;
  height: 40px;
  color: #f33;
  display: flex;
  align-items: center;
`;
const InputSettingArea = styled.div`
  width: 100%;
`;
const InputRow = styled.div`
  width: 100%;
  display: flex;
  align-items: flex-start;
`;
const InputArea = styled.div`
  width: 300px;
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 10px;
`;
const TextInput = styled.input`
  width: 200px;
  height: 40px;
  border-radius: 3px;
  border: 1px solid ${props => props.isError ? '#f33' : '#ddd'};
  color: ${props => props.isError ? '#f33' : '#ddd'};
  color: #565151;
  font-size: 14px;
  padding: 10px;
`;
const SelectWrapper = styled.div`
  position: relative;
  width: 200px;
  height: 40px;
  border-radius: 3px;
  border: 1px solid ${props => props.isError ? '#f33' : '#ddd'};
  color: ${props => props.isError ? '#f33' : '#ddd'};
  background-color: #fff;
`;
const Select = styled.select`
  -webkit-appearance: none;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border: 0;
  padding: 10px;
`;
const ArrowWrapper = styled.div`
  width: 40px;
  height: 40px;
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  align-items: center;;
  justify-content: center;
  user-select: none;
  pointer-events: none;
  svg {
    color: #ccc;
    font-size: 22px;
  }
`;
const Label = styled.div`
  width: 80px;
  margin-right: 10px;
  font-size: 14px;
  color: #2a2a2a;
  text-align: right;
`;
const RadioWrapper = styled.div`
  width: 200px;
  height: 40px;
  display: flex;
`;
const RadioBtn = styled.div`
    /* width: 145px; */
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    border-radius: 3px;
    border: 1px solid ${props => props.isError ? '#f33' : props.active ? '#333' : '#ddd'};
    color: ${props => props.active ? '#333' : '#ddd'};
    margin-right: 10px;
    &:last-child {
        margin-right: 0;
    }
    cursor: pointer;
`;

const AddOptionsArea = styled.div`
  width: 550px;
  display: flex;
  align-items: flex-start;
  position: relative;
  margin-bottom: 10px;
  padding-right: 40px;
  flex-wrap: wrap;
`;
const Options = styled.div`
  width: 500px;
  height: 40px;
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 10px;
  &:last-child {
    margin-bottom: 0;
  }
`;
const OptionInput = styled(TextInput)`
  width: 120px;
`;
const OptionSetting = styled.div`
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  svg {
    font-size: 22px;
    color: #565151;
  }
`;
const CreateBtn = styled.div`
    width: 145px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    border-radius: 3px;
    font-size: 14px;
    border: 1px solid ${props => props.isError ? '#f33' : '#333'};
    color: ${props => props.isError ? '#f33' : '#333'};
    margin-right: 10px;
    &:last-child {
        margin-right: 0;
    }
    cursor: pointer;
`;
const FormArea = styled.div`
  width: 100%;
`;

class FormSettingView extends Component {
  constructor() {
    super()
    this.state = {
      groupTitle: undefined,
      groupId: undefined,
      groups: [],
      isGroupErr: false,
      groupErrMsg: '',
      selectedGroup: undefined,
      columnName: undefined,
      columnId: undefined,
      columnType: undefined,
      required: false,
      colspan: undefined,
      isShowMultipleOptions: false,
      options: [
        {
          label: '',
          value: ''
        }
      ],
      requiredMap: ['columnName', 'columnId', 'columnType', 'selectedGroup'],
      inputsRequiredMap: {},
      groupErrorMap: {},
      errMap: {},
      optionsErrMap: {},
      formConfig: {}
    }
    this.addGroup = this.addGroup.bind(this)
    this.onTypeChange = this.onTypeChange.bind(this)
    this.handleInput = this.handleInput.bind(this)
    this.addOption = this.addOption.bind(this)
    this.removeOption = this.removeOption.bind(this)
    this.setOption = this.setOption.bind(this)
    this.createColumn = this.createColumn.bind(this)
    this.resetSetting = this.resetSetting.bind(this)
    this.setColspan = this.setColspan.bind(this)
  }
  addGroup() {
    const {
      groupTitle,
      groupId,
      groups
    } = this.state

    if(!groupTitle || !groupId) {
      this.setState({
        isGroupErr: true,
        groupErrMsg: '請輸入必填欄位'
      })
      return
    }
    if(groups.indexOf(groupId) > 0) {
      this.setState({
        isGroupErr: true,
        groupErrMsg: '群組 id 重複，請填寫唯一的群組 id'
      })
      return
    }

    let new_groups = Object.assign([], groups)
    new_groups.push({
      groupId,
      groupTitle
    })
    this.setState({
      groupId: undefined,
      groupTitle: undefined,
      isGroupErr: false,
      groupErrMsg: '',
      groups: new_groups
    })

  }
  selectGroup(e) {
    this.setState({
      selectedGroup: e.target.value
    })
  }
  onTypeChange(e) {
    if(e.target.value === 'select' ||
      e.target.value === 'checkbox' ||
      e.target.value === 'radio') {
      this.setState({
        isShowMultipleOptions: true,
        columnType: e.target.value,
        errMap: {},
        optionsErrMap: {}
      })
    } else {
      this.setState({
        isShowMultipleOptions: false,
        columnType: e.target.value,
        errMap: {},
        optionsErrMap: {}
      })
    }
  }
  handleInput(fieldName, e) {
    this.setState({
      [fieldName]: e.target.value,
      errMap: {},
      optionsErrMap: {},
    })
  }
  addOption() {
    const {
      options
    } = this.state
    let new_options = Object.assign([], options)
    let new_option = {
      label: '',
      value: ''
    }
    new_options.push(new_option)
    this.setState({
      options: new_options,
      errMap: {},
      optionsErrMap: {}
    })
  }
  removeOption(idx) {
    const {
      options
    } = this.state
    let new_options = Object.assign([], options)
    new_options.splice(idx, 1)
    this.setState({
      options: new_options,
      errMap: {},
      optionsErrMap: {}
    })
  }
  setOption(fieldName, idx, e) {
    const {
      options
    } = this.state
    let new_options = Object.assign([], options)
    new_options[idx][fieldName] = e.target.value
    this.setState({
      options: new_options,
      errMap: {},
      optionsErrMap: {}
    })
  }
  setRequired(isRequired) {
    this.setState({
      required: isRequired,
      errMap: {},
      optionsErrMap: {}
    })
  }
  setColspan(colspan) {
    this.setState({
      colspan,
      errMap: {},
      optionsErrMap: {}
    })
  }
  createColumn() {
    const {
      selectedGroup,
      columnName,
      columnId,
      columnType,
      options,
      required,
      colspan,
      requiredMap,
      inputsRequiredMap,
      formConfig
    } = this.state

    let errMap = {}
    let optionsErrMap = {}
    for (let i = 0; i < requiredMap.length; i++) {
      let requiredColumn = this.state[requiredMap[i]]
      if (!requiredColumn || JSON.stringify(requiredColumn) === '{}' || requiredColumn.length === 0) {
        errMap[requiredMap[i]] = true
      }
    }
    if(columnType === 'checkbox' || columnType === 'radio' || columnType === 'select') {
      for (let i = 0; i < options.length; i++) {
        if(!options[i].label || !options[i].value) {
          optionsErrMap[i] = true
        }
      }
    }
    console.log(errMap)
    if (JSON.stringify(errMap) !== '{}') {
      this.setState({
        errMap,
        optionsErrMap
      })
      return
    }
    let new_groupRequiredMap = Object.assign([], inputsRequiredMap[selectedGroup])
    if (required) {
      new_groupRequiredMap.push(columnId)
    }
    const new_inputsRequiredMap = Object.assign({}, inputsRequiredMap, {
      [selectedGroup]: new_groupRequiredMap
    })
    let new_groupConfig = Object.assign([], formConfig[selectedGroup])
    let new_column = {
      columnName,
      columnId,
      columnType,
      required,
      colspan,
    }
    if (Array.isArray(options) && options.length > 0 && options[0].label && options[0].value) {
      new_column.options = options
    }
    new_groupConfig.push(new_column)

    
    let new_formConfig = Object.assign({}, formConfig, {
      [selectedGroup]: new_groupConfig
    })
    this.setState({
      formConfig: new_formConfig,
      inputsRequiredMap: new_inputsRequiredMap
    }, () => {
      this.resetSetting()
    })
  }
  resetSetting() {
    this.setState({
      columnName: undefined,
      columnId: undefined,
      columnType: undefined,
      colspan: undefined,
      required: false,
      selectedGroup: undefined,
      isShowMultipleOptions: false,
      options: [
        {
          label: '',
          value: ''
        }
      ],
      errMap: {},
      optionsErrMap: {},
    })
  }
  render() {
    const {
      groups,
      groupId,
      groupTitle,
      groupErrMsg,
      isGroupErr,
      selectedGroup,
      columnName,
      columnId,
      columnType,
      required,
      colspan,
      formConfig,
      isShowMultipleOptions,
      options,
      errMap,
      optionsErrMap
    } = this.state
    return (
      <Container>
        <GroupSettingArea>
          <InputRow>
            <InputArea>
              <Label>
                群組名稱
              </Label>
              <TextInput 
                onChange={e => this.handleInput('groupTitle', e)}
                value={groupTitle || ''}
                isError={isGroupErr}
              />
            </InputArea>
            <InputArea>
              <Label>
                群組id
              </Label>
              <TextInput
                onChange={e => this.handleInput('groupId', e)}
                value={groupId || ''}
                isError={isGroupErr}
              />
            </InputArea>
            <CreateBtn
              onClick={this.addGroup}
            >
              新增群組
            </CreateBtn>
            <ErrMsg>
              {groupErrMsg}
            </ErrMsg>
            
          </InputRow>
        </GroupSettingArea>
        {Array.isArray(groups) && groups.length > 0 ?
          <InputSettingArea>
            <InputRow>
              <InputArea>
                <Label>
                  選擇群組
              </Label>
                <SelectWrapper
                  isError={errMap['selectedGroup']}
                >
                  <Select
                    value={selectedGroup || ''}
                    onChange={e => this.selectGroup(e)}
                  >
                    <option value="" disabled>請選擇群組</option>
                    {groups.map((group, idx) => {
                      const {
                        groupId,
                        groupTitle
                      } = group
                      return (
                        <option key={groupId} value={groupId}>{groupTitle}</option>
                      )
                    })
                    }
                  </Select>
                  <ArrowWrapper>
                    <IoIosArrowDown />
                  </ArrowWrapper>
                </SelectWrapper>
              </InputArea>
              <InputArea>
                <Label>
                  欄位名稱
              </Label>
                <TextInput
                  onChange={e => this.handleInput('columnName', e)}
                  value={columnName || ''}
                  isError={errMap['columnName']}
                />
              </InputArea>
              <InputArea>
                <Label>
                  欄位id
            </Label>
                <TextInput
                  onChange={e => this.handleInput('columnId', e)}
                  value={columnId || ''}
                  isError={errMap['columnId']}
                />
              </InputArea>
            </InputRow>
            <InputRow>
              <InputArea>
                <Label>
                  欄位類型
            </Label>
                <SelectWrapper
                  isError={errMap['columnType']}
                >
                  <Select
                    value={columnType || ''}
                    onChange={e => this.onTypeChange(e)}
                  >
                    <option value="" disabled>請選擇欄位類型</option>
                    <option value="text" >純文字</option>
                    <option value="phone">電話</option>
                    <option value="address">地址</option>
                    <option value="radio">單選</option>
                    <option value="checkbox">多選</option>
                    <option value="date">日期</option>
                    <option value="select">下拉選單</option>
                    <option value="textarea">文字輸入盒</option>
                  </Select>
                  <ArrowWrapper>
                    <IoIosArrowDown />
                  </ArrowWrapper>
                </SelectWrapper>
              </InputArea>
              {isShowMultipleOptions ?
                <AddOptionsArea>
                  {Array.isArray(options) && options.length > 0 && options.map((option, idx) => {
                    if (idx === 0) {
                      return (
                        <Options
                          key={idx}
                        >
                          <Label>選項名稱</Label>
                          <OptionInput
                            onChange={e => this.setOption('label', idx, e)}
                            isError={optionsErrMap[idx]}
                          />
                          <Label>選項值</Label>
                          <OptionInput
                            onChange={e => this.setOption('value', idx, e)}
                            isError={optionsErrMap[idx]}
                          />
                          <OptionSetting
                            onClick={this.addOption}
                          >
                            <IoIosAddCircleOutline />
                          </OptionSetting>
                        </Options>
                      )
                    } else {
                      return (
                        <Options
                          key={idx}
                        >
                          <Label>選項名稱</Label>
                          <OptionInput
                            onChange={e => this.setOption('label', idx, e)}
                            isError={optionsErrMap[idx]}
                          />
                          <Label>選項值</Label>
                          <OptionInput
                            onChange={e => this.setOption('value', idx, e)}
                            isError={optionsErrMap[idx]}
                          />
                          <OptionSetting
                            onClick={() => this.removeOption(idx)}
                          >
                            <IoMdRemoveCircleOutline />
                          </OptionSetting>
                        </Options>
                      )
                    }
                  })

                  }
                </AddOptionsArea>
                :
                null
              }
            </InputRow>
            <InputRow>
              <InputArea>
                <Label>
                  是否必填
                </Label>
                <RadioWrapper>
                  <RadioBtn
                    onClick={() => this.setRequired(true)}
                    active={required}
                  >是</RadioBtn>
                  <RadioBtn
                    onClick={() => this.setRequired(false)}
                    active={!required}
                  >否</RadioBtn>
                </RadioWrapper>
              </InputArea>
              <InputArea>
                <Label>
                  佔位數
                </Label>
                <RadioWrapper>
                  <RadioBtn
                    onClick={() => this.setColspan(1)}
                    active={colspan === 1 || !colspan}
                  >1</RadioBtn>
                  <RadioBtn
                    onClick={() => this.setColspan(2)}
                    active={colspan === 2}
                  >2</RadioBtn>
                  <RadioBtn
                    onClick={() => this.setColspan(3)}
                    active={colspan === 3}
                  >3</RadioBtn>
                </RadioWrapper>
              </InputArea>
            </InputRow>
            <InputRow>
              <CreateBtn
                onClick={this.createColumn}
              >
                新增
              </CreateBtn>
            </InputRow>

          </InputSettingArea>
          :
          null

        }
        <FormArea>
          {groups.map(group => {
            const {
              groupId,
              groupTitle
            } = group
            const inputsConfig = formConfig[groupId]
            if (inputsConfig) return (
              <FormGroup
                groupTitle={groupTitle}
                inputs={formConfig[groupId]}
                key={groupId}
                errMap={{}}
              />
            )
            else return null
          })

          }
        </FormArea>
        <pre>
          {JSON.stringify(formConfig, null, 2)}
        </pre>
      </Container>
    )
  }
}


export default FormSettingView