/*--------------------------
    Import
--------------------------*/
import React, { Component } from 'react';
import styled from 'styled-components';
import GLOBAL from '../../global'
import { IoIosArrowDown } from "react-icons/io";

/*--------------------------
    Styled
--------------------------*/
const Wrapper = styled.div`
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;

`;
const SelectWrapper = styled.div`
    width: 155px;
    position: relative;
    margin-right: 10px;
`;
const Arrow = styled.div`
    position: absolute;
    right: 0;
    top: 0;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    pointer-events: none;
    svg {
        font-size: 20px;
        color: #cdcdcd;
    }
`;
const Select = styled.select`
    width: 100%;
    border: 1px solid ${props => props.isError ? '#f33' : '#ddd'};
    height: 40px;
    line-height: 39px;
    border-radius: 3px;
    background-color: #fff;
    font-size: 14px;
    color: ${
  props => props.hasValue ?
    '#333'
    :
    '#777'
  };
    padding-left: 10px;
    -webkit-appearance: none;
`;
const Postal = styled.div`
    width: 90px;
    height: 40px;
    border: 1px solid ${props => props.isError ? '#f33' : '#ddd'};
    border-radius: 3px;
    font-size: 14px;
    padding-left: 10px;
    margin-right: 10px;
    display: flex;
    align-items: center;
    color: #333;
`;
const AddressInput = styled.input`
    flex: 2;
    height: 40px;
    border: 1px solid ${props => props.isError ? '#f33' : '#ddd'};
    height: 40px;
    border-radius: 3px;
    background-color: #fff;
    font-size: 14px;
    padding-left: 10px;
    color: #333;
`;
const VillagePostal = styled.input`
    width: 90px;
    height: 40px;
    border: 1px solid ${props => props.isError ? '#f33' : '#ddd'};
    border-radius: 3px;
    font-size: 14px;
    padding-left: 10px;
    margin-right: 10px;
    color: #333;
`;
/*--------------------------
    Main Component
--------------------------*/
class AddressInputs extends Component {

  // static propTypes = {
  // };

  constructor(props) {
    super(props);

    this.state = {
      city: '',
      regions: '',
      postal: '',
      address: ''
    };
    this.init = this.init.bind(this)
    this.handleInput = this.handleInput.bind(this)
    this.handleRegion = this.handleRegion.bind(this)
    this.returnAddress = this.returnAddress.bind(this)
  }

  componentDidMount() {
    this.init()
  }
  init() {
    const {
      address: addressObj
    } = this.props
    if (!addressObj) return
    let {
      city,
      regions,
      postal,
      address
    } = addressObj
    const villagePostal = postal.length > 3 ? postal.substr(-2, 2) : ''
    postal = postal.substr(0, 3)
    this.setState({
      city,
      regions,
      postal,
      villagePostal,
      address
    })
  }
  handleInput(field, e) {
    if (field === 'city') {
      const regions = GLOBAL.REGIONS[e.target.value]
      this.setState({
        city: e.target.value,
        regions: regions[0].value,
        postal: regions[0].zipcode,
        villagePostal: '',
      }, () => {
        this.returnAddress()
      })
    } else {
      this.setState({
        [field]: e.target.value,
      }, () => {
        this.returnAddress()
      })

    }
  }
  handleRegion(field, e) {
    if (!this.state.city) return
    const regions = GLOBAL.REGIONS[this.state.city].filter((item) => {
      return item.label === e.target.value
    })
    this.setState({
      [field]: e.target.value,
      postal: regions[0].zipcode,
      villagePostal: '',
      errMsg: '',
      isError: false,
    }, () => {
      this.returnAddress()
    })
  }
  returnAddress() {
    const {
      onAddressChange,
      setAddressError,
    } = this.props
    const {
      city,
      regions,
      postal,
      villagePostal,
      address
    } = this.state
    if (!city || !regions || !postal || !villagePostal || !address) {
      setAddressError && setAddressError()
      return
    }
    onAddressChange && onAddressChange({
      city,
      regions,
      postal: postal + villagePostal,
      address
    })
  }

  render() {
    const {
      city,
      regions,
      postal,
      address,
      villagePostal
    } = this.state
    const {
      isError
    } = this.props
    return (
      <Wrapper>
        <SelectWrapper>
          <Select
            onChange={e => this.handleInput('city', e)}
            value={city}
            hasValue={city}
            isError={isError}
          >
            <option value="" disabled>縣市</option>
            {GLOBAL.CITIES.map(city => {
              const {
                label,
                key,
                value
              } = city
              return (
                <option
                  key={key}
                  value={value}
                >
                  {label}
                </option>
              )
            })

            }

          </Select>
          <Arrow>
            <IoIosArrowDown />
          </Arrow>
        </SelectWrapper>
        <SelectWrapper>
          <Select
            onChange={e => this.handleRegion('regions', e)}
            value={regions}
            hasValue={regions}
            isError={isError}
          >
            <option value="" disabled>鄉鎮市區</option>
            {city ?
              GLOBAL.REGIONS[this.state.city].map((regions, idx) => {
                const label = regions.label
                const value = regions.value
                return (
                  <option
                    key={idx}
                    value={value}
                  >
                    {label}
                  </option>
                )
              })
              :
              null
            }
          </Select>
          <Arrow>
            <IoIosArrowDown />
          </Arrow>
        </SelectWrapper>
        <Postal
          isError={isError}
        >
          {postal}
        </Postal>
        <VillagePostal
          onChange={e => this.handleInput('villagePostal', e)}
          value={villagePostal || ''}
          maxLength="2"
          isError={isError}
        />
        <AddressInput
          onChange={e => this.handleInput('address', e)}
          value={address || ''}
          isError={isError}
        />
      </Wrapper>
    );
  }
}

/*--------------------------
    Export
--------------------------*/
export default AddressInputs