
/*--------------------------
    Import
--------------------------*/
import React, { Component } from 'react';
import styled from 'styled-components';
import { IoIosArrowDown, IoMdRemoveCircleOutline } from "react-icons/io";
/*--------------------------
    Styled
--------------------------*/
const Container = styled.div`
    width: 100%;
`;
const Row = styled.div`
    width: 100%;
    /* padding-right: 40px; */
    position: relative;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    &:last-child {
        margin-bottom: 0px;
    }
`;
const SelectArea = styled.div`
    /* flex: 2; */
    width: 100px;
    height: 40px;
    position: relative;
    margin-right: 10px;
`;
const Select = styled.select`
    width: 100%;
    height: 100%;
    border-radius: 3px;
    border: 1px solid ${props => props.isError ? '#f33' : '#ddd'};
    font-size: 14px;
    padding-left: 10px;
    color: #4C4C4E;
    background-color: #fff;
    -webkit-appearance: none;
`;
const SelectArrowWrapper = styled.div`
    width: 40px;
    height: 40px;
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    user-select: none;
    svg {
        font-size: 20px;
        color: #cdcdcd;
    }
`;
const InputSec1 = styled.input`
    width: 60px;
    height: 40px;
    border-radius: 3px;
    border: 1px solid ${props => props.isError ? '#f33' : '#ddd'};
    font-size: 14px;
    padding-left: 10px;
    color: #4C4C4E;
    margin-right: 10px;
`;
const InputSec2 = styled.input`
    flex: 2;
    height: 40px;
    border-radius: 3px;
    border: 1px solid ${props => props.isError ? '#f33' : '#ddd'};
    font-size: 14px;
    padding-left: 10px;
    color: #4C4C4E;
    margin-left: 10px;
    margin-right: 10px;
    &:last-child {
        margin-right: 0;
    }
`;
const InputSec3 = styled.input`
    width: 60px;
    height: 40px;
    border-radius: 3px;
    border: 1px solid ${props => props.isError ? '#f33' : '#ddd'};
    font-size: 14px;
    padding-left: 10px;
    color: #4C4C4E;
    margin-left: 10px;
`;
const AddPhoneBtn = styled.div`
    width: 100%;
    height: 40px;
    font-size: 14px;
    color: #333;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 3px;
    border: 1px dashed transparent;
    background: linear-gradient(white,white) padding-box,
    repeating-linear-gradient(-45deg,#ccc 0, #ccc 10px,white 0,white 20px);
    margin: 0 auto 0 auto;
    cursor: pointer;
`;
const DeleteBtn = styled.div`
    height: 40px;
    width: 40px;
    color: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 22px;
    cursor: pointer;
`;
/*--------------------------
    Main Component
--------------------------*/
class PhoneGroup extends Component {

  // static propTypes = {
  // };

  constructor(props) {
    super(props);

    this.state = {
      forms: [
        {
          type: '0',
          sec1: '',
          sec2: '',
          sec3: '',
        },
      ]
    };
    this.addPhone = this.addPhone.bind(this)
    this.selectChange = this.selectChange.bind(this)
    this.onInputChange = this.onInputChange.bind(this)
    this.setValue = this.setValue.bind(this)
    this.removeRow = this.removeRow.bind(this)
  }

  componentDidMount() {
    const {
      phones
    } = this.props
    if (!phones) return
    this.setState({
      forms: phones
    })
  }

  addPhone() {
    const {
      forms
    } = this.state
    let newForm = {
      type: '0',
      sec1: '',
      sec2: '',
      sec3: '',
    }
    this.setState({
      forms: forms.concat(newForm)
    })
  }
  selectChange(idx, e) {
    let {
      forms
    } = this.state
    let newForms = Object.assign([], forms)
    newForms[idx].type = e.target.value
    newForms[idx].sec1 = ''
    newForms[idx].sec2 = ''
    newForms[idx].sec3 = ''
    let phoneArr = []
    for (let i = 0; i < newForms.length; i++) {
      let num = ''
      num += newForms[i].sec1
      num += newForms[i].sec2
      if (newForms[i].sec3) {
        num += '#'
      }
      num += newForms[i].sec3
      phoneArr.push(num)
    }
    this.setState({
      forms: newForms
    })
    this.setValue()
  }
  onInputChange(idx, section, e) {
    let num = e.target.value
    let {
      forms
    } = this.state
    let newForms = Object.assign([], forms)
    if (/^\d*\.?\d*$/.test(num)) {
      newForms[idx][section] = num
    } else return
    let phoneArr = []
    for (let i = 0; i < newForms.length; i++) {
      let num = ''
      num += newForms[i].sec1
      num += newForms[i].sec2
      if (newForms[i].sec3) {
        num += '#'
      }
      num += newForms[i].sec3
      phoneArr.push(num)
    }
    console.log(phoneArr)
    this.setState({
      forms: newForms
    })
    this.setValue()
  }
  setValue() {
    const {
      onChange,
      setPhoneError
    } = this.props
    let {
      forms
    } = this.state

    for (let i = 0; i < forms.length; i++) {
      let form = forms[i]
      if (form.type === '0' || form.type === '2') {
        if (!form.sec1 || !form.sec2) {
          setPhoneError && setPhoneError()
          return
        }
      } else {
        if (!form.sec1 || !form.sec2 || !form.sec3) {
          setPhoneError && setPhoneError()
          return
        }
      }
    }

    onChange && onChange(forms)
  }
  removeRow(idx) {
    const {
      forms
    } = this.state
    let newForms = Object.assign([], forms)
    newForms.splice(idx, 1)
    this.setState({
      forms: newForms
    }, () => {
      this.setValue()
    })
  }

  render() {
    const {
      forms
    } = this.state
    const {
      isError
    } = this.props
    return (
      <Container>
        {forms.map((form, idx) => {
          const {
            type,
            sec1,
            sec2,
            sec3
          } = form
          if (idx === 0) {
            return (
              <Row
                key={idx}
              >
                <SelectArea>
                  <SelectArrowWrapper>
                    <IoIosArrowDown />
                  </SelectArrowWrapper>
                  <Select
                    onChange={e => this.selectChange(idx, e)}
                    isError={isError}
                  >
                    <option value="0">行動電話</option>
                    <option value="1">室內電話</option>
                    <option value="2">傳真</option>
                  </Select>
                </SelectArea>
                {type === '0' ?
                  <React.Fragment>
                    <InputSec1
                      onChange={e => this.onInputChange(idx, 'sec1', e)}
                      value={sec1}
                      isError={isError}
                    />
                    -
                                        <InputSec2
                      onChange={e => this.onInputChange(idx, 'sec2', e)}
                      value={sec2}
                      isError={isError}
                    />
                  </React.Fragment>
                  :
                  null
                }
                {type === '1' ?
                  <React.Fragment>
                    <InputSec1
                      onChange={e => this.onInputChange(idx, 'sec1', e)}
                      value={sec1}
                      isError={isError}
                    />
                    -
                                        <InputSec2
                      onChange={e => this.onInputChange(idx, 'sec2', e)}
                      value={sec2}
                      isError={isError}
                    />
                    #
                                        <InputSec3
                      onChange={e => this.onInputChange(idx, 'sec3', e)}
                      value={sec3}
                      isError={isError}
                    />
                  </React.Fragment>
                  :
                  null
                }
                {type === '2' ?
                  <React.Fragment>
                    <InputSec1
                      onChange={e => this.onInputChange(idx, 'sec1', e)}
                      value={sec1}
                      isError={isError}
                    />
                    -
                                        <InputSec2
                      onChange={e => this.onInputChange(idx, 'sec2', e)}
                      value={sec2}
                      isError={isError}
                    />
                  </React.Fragment>
                  :
                  null
                }
              </Row>
            )
          } else {
            return (
              <Row
                key={idx}
              >
                <DeleteBtn
                  onClick={() => this.removeRow(idx)}
                >
                  <IoMdRemoveCircleOutline />
                </DeleteBtn>
                <SelectArea>
                  <SelectArrowWrapper>
                    <IoIosArrowDown />
                  </SelectArrowWrapper>
                  <Select
                    value={type}
                    onChange={e => this.selectChange(idx, e)}
                    isError={isError}
                  >
                    <option value="0">行動電話</option>
                    <option value="1">室內電話</option>
                    <option value="2">傳真</option>
                  </Select>
                </SelectArea>
                {type === '0' ?
                  <React.Fragment>
                    <InputSec1
                      onChange={e => this.onInputChange(idx, 'sec1', e)}
                      value={sec1}
                      isError={isError}
                    />
                    -
                                        <InputSec2
                      onChange={e => this.onInputChange(idx, 'sec2', e)}
                      value={sec2}
                      isError={isError}
                    />
                  </React.Fragment>
                  :
                  null
                }
                {type === '1' ?
                  <React.Fragment>
                    <InputSec1
                      onChange={e => this.onInputChange(idx, 'sec1', e)}
                      value={sec1}
                      isError={isError}
                    />
                    -
                                        <InputSec2
                      onChange={e => this.onInputChange(idx, 'sec2', e)}
                      value={sec2}
                      isError={isError}
                    />
                    #
                                        <InputSec3
                      onChange={e => this.onInputChange(idx, 'sec3', e)}
                      value={sec3}
                      isError={isError}
                    />
                  </React.Fragment>
                  :
                  null
                }
                {type === '2' ?
                  <React.Fragment>
                    <InputSec1
                      onChange={e => this.onInputChange(idx, 'sec1', e)}
                      value={sec1}
                      isError={isError}
                    />
                    -
                                        <InputSec2
                      onChange={e => this.onInputChange(idx, 'sec2', e)}
                      value={sec2}
                      isError={isError}
                    />
                  </React.Fragment>
                  :
                  null
                }
              </Row>
            )
          }
        })

        }
        <Row>
          <AddPhoneBtn
            onClick={this.addPhone}
          >新增電話</AddPhoneBtn>
        </Row>
      </Container>
    );
  }
}


/*--------------------------
    Export
--------------------------*/
export default PhoneGroup