function getPlaylistItemsStart(payload = {}) {
    return {
        type: actionTypes.getPlaylistItemsStart,
        payload
    }
}


function getPlaylistItemsError(payload = {}) {
    return {
        type: actionTypes.getPlaylistItemsError,
        payload
    }
}

function getPlaylistItemsSuccess(data) {
    const payload = {
        items: data.items,
        pageInfo: data.pageInfo,
        nextPageToken: data.nextPageToken
    }
    return {
        type: actionTypes.getPlaylistItemsSuccess,
        payload
    }
}

function getPlaylistItems(payload) {
    const {
        nextPageToken
    } = payload
    return dispatch => {
        dispatch(getPlaylistItemsStart())
        let url = 'https://www.googleapis.com/youtube/v3/playlistItems'
        url += '?part=snippet'
        url += '&playlistId=RDEMtn0sHfPuCTd6JF5sYi_a9A'
        url += '&maxResults=10'
        if(nextPageToken) url += '&pageToken=' + nextPageToken
        url += '&key=' + GLOBAL.KEY
        fetch(url, {method: 'get'})
        .then(response => {
            response.json()
            .then(result => {
                const {
                    items,
                    nextPageToken,
                    pageInfo
                } = result
                dispatch(getPlaylistItemsSuccess({items, nextPageToken, pageInfo}))
            })
            .catch(err => {
                dispatch(getPlaylistItemsError())
            })
        })
        .catch(err => {
            dispatch(getPlaylistItemsError())
        })
    }
}


const playlistActions = {
    // addTodo,
    getPlaylistItems
}

export default playlistActions